@import url(https://fonts.googleapis.com/css?family=Dancing+Script);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  /* background: #222; */
  background: url(/react-todolist/static/media/background.1ee55e5c.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  color: #fff;
  font-family: "Quicksand", sans-serif;
  font-size: 18px;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

#root{
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.App{
  width: 100%;
  height: 100%;
  position: relative;
}


.Notebook {
  /* margin: 5em auto; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  text-transform: uppercase;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 10;
}

.notebook-wrapper {
  max-width: calc(100% / 2);
  min-width: 40em;
  max-height: calc (100%);
  min-height: 100%;
}

.notebook {
  height: 40em;
  width: 70%;
  position: relative;
  transition: 0.4s ease-in-out;
  border-radius: 5px 15px 15px 5px;
  -webkit-transform-origin: left center 0px;
          transform-origin: left center 0px;
  display: inline-block;
  margin: 30px;
  -webkit-perspective: 2000px;
          perspective: 2000px;
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
}

